@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #efefef;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}
html {
  font-size: 62.5%;
}
a {
  text-decoration: none;
  color: black;
}

.btn {
  background: #b3e140;
  padding: 2rem 5rem;
  border-radius: 1.5rem;
  display: grid;
  place-items: center;
  cursor: pointer;
  border: none;
  outline: none;
  color: white;
  color: rgb(255, 255, 255);
  font-size: 1.2rem;
  font-weight: bold;
  margin: 0 1rem;
  box-shadow: 0 0.5rem 1rem rgba(34, 34, 34, 0.233);
  border: 1px solid rgba(0, 0, 0, 0.103);
}
.btn:disabled {
  background: #eee;
  color: rgb(216, 213, 213);
  cursor: not-allowed;
}

@media (max-width: 540px) {
  .btn {
    width: 200px;
    padding: 1.5rem 1rem;
  }
}

.cartpage {
  width: 100%;
  height: calc(100vh - 9rem);

  padding: 0 12rem;
  overflow-y: scroll;
}
.cartpage::-webkit-scrollbar {
  display: none;
}

.total-price {
  font-size: 4rem;
  font-weight: 700;
  color: #181a13;
  margin-right: 1rem;
  display: flex;
}
.checkout-btn {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.checkout-btn p {
  font-size: 2rem;
  font-weight: bold;
}

/* *******************media queries************* */
@media (max-width: 1024px) {
  .cartpage {
    padding: 0 2rem;
  }
}
@media (max-width: 650px) {
  .cartpage {
    padding: 0 0.5rem;
  }
}

.product {
  width: 100%;
  position: relative;
  height: 30rem;
  padding: 2rem;
  display: flex;

  align-items: center;
  justify-content: space-between;
  border-radius: 2rem;
  box-shadow: 0 0.5rem 1rem rgba(34, 34, 34, 0.233);
  background: #fff;
}
.bin {
  position: absolute;
  top: 20px;
  right: 20px;
}
.bin:hover svg {
  color: red;
}
.bin svg {
  font-size: 2rem;
  cursor: pointer;
}
.product-left {
  display: flex;
  height: 100%;
  align-items: center;
}
.product-img {
  width: 30rem;
  height: 20rem;
  overflow: hidden;
  border-radius: 5rem;
  margin-right: 2rem;
}
.product-img img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.info-name {
  font-weight: bold;
  font-size: 3rem;
}
.info-color {
  font-size: 1.4rem;
  font-weight: bold;
}
.order-count {
  display: flex;
  align-items: center;
  margin-top: 1rem;
}
.order-count p {
  margin: 0 1rem;
  font-size: 2.5rem;
  font-weight: bold;
}
.order-count button {
  width: 4rem;
  height: 4rem;
  font-size: 2rem;
  font-weight: bold;
  border: none;
  outline: none;
  background: #b3e140;
  border-radius: 1rem;
  cursor: pointer;
}
.order-count button:disabled {
  background-color: #eee;
}
.sizes {
  display: flex;
  margin-top: 1rem;
  flex-wrap: wrap;
}
.size {
  background: white;
  outline: none;
  border: none;
  padding: 1rem 2.5rem;
  margin: 5px 5px;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
  box-shadow: 0 0.2rem 1rem rgba(34, 34, 34, 0.233);
}

.size:hover {
  background: #b3e140;
}
.product-right p {
  font-size: 3rem;
  font-weight: 600;
  text-align: right;
}
.product-right span {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 4px;
}
.product-price {
  display: flex;
  align-items: flex-end;
}

/* *******************media queries************* */

@media (max-width: 1024px) {
  .product {
    flex-direction: column;
  }
  .product-img {
    width: 24rem;
    height: 15rem;

    border-radius: 2rem;
    margin-right: 2rem;
  }
  .info-name {
    font-weight: bold;
    font-size: 2rem;
  }
  .product-right span {
    font-size: 1rem !important;
    font-weight: 700;
  }
  .product-right p {
    font-size: 2rem;
    font-weight: 700;
    text-align: right;
  }
}

@media (max-width: 660px) {
  .product-left {
    align-items: flex-start;
  }
  .product-img {
    width: 20rem;
    height: 10rem;
    overflow: hidden;
    display: none;

    border-radius: 2rem;
    margin-right: 2rem;
  }
  .size {
    padding: 1rem 1.5rem;
    font-size: 1rem;
    font-weight: 300;
  }
  .order-count button {
    width: 3rem;
    height: 3rem;
    border-radius: 2px;
  }
  .order-count {
    margin-bottom: 5px;
  }
  .product-right {
    display: flex;
    align-items: flex-end;
    margin-top: 10px;
  }
  .product-price {
    margin-left: 5px;
  }
  .total-price {
    display: none;
  }
}
@media (max-width: 660px) {
}

.bottombar {
  background: rgba(199, 199, 199, 0.308);
  width: 100%;
  position: fixed;
  left: 0;
  right: 0;
  padding: 1rem 12rem;
  bottom: 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 8rem;
}
@media (max-width: 960px) {
  .bottombar {
    justify-content: center;
  }
}

.header {
  height: 6rem;
  position: sticky;
  top: 0;
  z-index: 999999;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 12rem;
  overflow: hidden;
}
.header-right {
  display: flex;
  height: 100%;
  align-items: center;
}
.header-right > * {
  margin: 0 1rem;
}
.message {
  position: absolute;
  right: -50rem;
  background: rgb(255, 255, 255);
  padding: 1rem 8rem;
  border-radius: 10px;
  box-shadow: 0 0.5rem 0.5rem #8dc100;
  color: #b3e140;
  font-weight: 700;
  -webkit-animation: slide 3s ease-in-out infinite;
          animation: slide 3s ease-in-out infinite;
}
@-webkit-keyframes slide {
  20% {
    right: 1rem;
  }
  80% {
    right: 1rem;
  }
}
@keyframes slide {
  20% {
    right: 1rem;
  }
  80% {
    right: 1rem;
  }
}
.logo {
  height: 5rem;
  cursor: pointer;
}
.logo img {
  height: 100%;
}
.login {
  background: #191b16;
  padding: 0.7rem 3rem;
  border-radius: 10px;
  cursor: pointer;
  outline: none;
  border: none;
}
.login p {
  color: #fff;

  font-weight: 700;
  font-size: 1.2rem;
}
.cart svg {
  font-size: 2.5rem;
  color: black;
}
.cart {
  position: relative;
  height: -webkit-max-content;
  height: max-content;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.cart-count {
  position: absolute;
  color: #000;
  font-weight: 700;
  background: white;
  border: 2px solid #b3e140;
  height: 2rem;
  width: 2rem;
  border-radius: 2rem;
  top: -1rem;
  right: -7px;
  display: grid;
  place-items: center;
}

/* *****************media queries********************** */

@media (max-width: 630px) {
  .header {
    padding: 1rem 8rem;
  }
  .message {
    padding: 1rem 3rem;
  }

  .message p {
    font-size: 1rem;
  }
}
@media (max-width: 456px) {
  .header {
    padding: 1rem 5rem;
  }
}
@media (max-width: 375px) {
  .header {
    padding: 1rem 2rem;
  }
  .login {
    background: #191b16;
    padding: 0.7rem 2rem;
  }
}

.hero {
  height: calc(100vh - 6rem);
  width: 100%;
  background: #b3e140;
  object-fit: contain;
  background-size: cover;
  position: relative;
  padding-top: 3rem;
}
.background {
  width: 100%;
  height: 100%;
  object-fit: cover;
  overflow: hidden;
  position: absolute;
  bottom: 0;
  pointer-events: none;
}

.message p {
  font-size: 1.5rem;
}
.background img {
  width: 110%;
  height: 100%;
  object-fit: cover;
  margin-top: 10rem;
}
.heading-top,
.heading-bottom {
  width: 100%;
  text-align: center;
  z-index: 2;
  position: relative;
}
.heading-top h1 {
  font-weight: 300;
  font-size: 6rem;
  letter-spacing: 3rem;
  color: white;
}
.heading-bottom h1 {
  z-index: 2;
  font-weight: 700;
  font-size: 12rem;
  color: #191b16;
  text-shadow: 2px 2px 10px #8dc100;
}
.checkout {
  background: #fff;
  border-radius: 2rem;
  margin: 0 auto;
  height: 8rem;
  width: 25rem;
  z-index: 2;
  position: relative;
  display: grid;
  place-items: center;
  box-shadow: 0 1rem 2rem #8dc100;
  cursor: pointer;
  -webkit-animation: bounce 3s ease-in-out infinite;
          animation: bounce 3s ease-in-out infinite;
}
.checkout p {
  font-size: 2.5rem;
  font-weight: 700;
  color: #181a13;
}
.price {
  position: absolute;
  z-index: 2;
  left: 20rem;
}
.price p {
  font-size: 4rem;
  color: white;
}
.price h1 {
  font-size: 14rem;
  color: white;
  margin-top: -5rem;
  text-shadow: 2px 2px 10px #8dc100;
}
@-webkit-keyframes bounce {
  25% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  30% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  35% {
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  40% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
@keyframes bounce {
  25% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  30% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  35% {
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  40% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

/* *******************media queries*******************8  */

@media (max-width: 1024px) {
  .heading-top h1 {
    font-size: 3rem;
    margin-top: 1rem;
  }
  .heading-bottom h1 {
    font-size: 8rem;
  }
  .checkout {
    height: 8rem;
    width: 25rem;
    margin-top: 18rem;
  }
  .checkout p {
    font-size: 2.5rem;
    font-weight: 700;
    color: #181a13;
  }
  .price {
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    top: 23rem;
  }
  .price p {
    font-size: 2rem;

    text-align: center;
  }
  .price h1 {
    font-size: 8rem;
    color: white;
    margin-top: -2rem;
    text-shadow: 2px 2px 10px #8dc100;
  }
}
@media (max-width: 630px) {
  .heading-top h1 {
    font-size: 2rem;
    margin-top: 1rem;
    letter-spacing: 2rem;
  }
  .heading-bottom h1 {
    font-size: 4rem;
  }
  .price {
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    top: 16rem;
  }
}
@media (max-width: 630px) {
  .price {
    top: 18rem;
  }
  .checkout {
    height: 6rem;
    width: 20rem;
  }
  .checkout p {
    font-size: 2rem;
  }
}
@media (max-width: 375px) {
  .heading-top h1 {
    font-size: 1.8rem;
    letter-spacing: 1rem;
    margin-top: 4.5rem;
  }
}

.login-page {
  display: grid;
  place-items: center;
  height: calc(100vh - 6rem);
}
.login-container {
  background: #b3e140;
  min-width: 50rem;
  height: 70vh;
  display: flex;
  flex-direction: column;
  box-shadow: 0 0.2rem 1rem rgba(34, 34, 34, 0.116);
  border-radius: 1rem;
  align-items: center;
  padding: 3rem;
}
.login-img {
  width: 20rem;
  height: 20rem;
}
.login-img img {
  width: 100%;
  object-fit: cover;
}
.form {
  width: 90%;
  display: flex;
  flex-direction: column;
}
input[type='email'],
input[type='password'],
input[type='text'] {
  background: rgb(255, 255, 255);
  border: none;
  outline: none;
  height: 4rem;
  margin: 1rem;
  border-radius: 1rem;
  padding: 0 2rem;
  font-size: 1.5rem;
  transition: 0.3s;
  box-shadow: 0 0.2rem 1rem rgba(34, 34, 34, 0.116);
}
/* input[type=email]:focus,input[type=password]:focus{
   height: 5rem;
} */
.submit {
  width: 50%;
  display: block;
  margin: 0 auto;
  height: 4rem;
  background: #000;
  color: white;
  font-weight: bold;
  font-size: 1.5rem;
  border: none;
  outline: none;
  border-radius: 1rem;
  cursor: pointer;
  box-shadow: 0 0.6rem 1rem rgba(34, 34, 34, 0.171);
}
.submit:disabled {
  background: rgb(219, 219, 219);
  cursor: not-allowed;
}
.submit a {
  color: white;
}
.error {
  color: rgb(209, 26, 26);
  margin: 0 1rem;
  margin-bottom: 1rem;
  font-weight: bold;
}

.address-page {
  height: calc(100vh - 6rem);
  width: 100%;
  /* background: red; */
  padding: 6rem 8rem;
  display: flex;
  position: relative;
}
.address-form {
  background-color: rgb(255, 255, 255);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.239);
  border-radius: 2rem;

  height: -webkit-max-content;

  height: max-content;
  width: 60%;
  padding: 2rem;
}
.address-form h1 {
  padding: 0 2rem;
  font-size: 3rem;
}
.address-form form {
  display: flex;
  flex-direction: row;
  padding: 2rem;
  flex-wrap: wrap;
  justify-content: space-between;
}
.form-field {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.address-form form label {
  margin-bottom: 0.5rem;
  margin-left: 0.5rem;
}
.address-form form input {
  max-width: 50rem;
  border-radius: 0px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.239);
  margin-bottom: 10px;
  /* border-bottom: 3px solid rgb(178, 229, 245); */
}
.address-form form input:focus {
  height: 4rem;
  border-bottom: 3px solid rgb(52, 233, 52);
}
.product-details-address {
  width: 40%;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.239);
  border-radius: 2rem;
  margin: 0 2rem;
  height: -webkit-max-content;
  height: max-content;
  display: flex;
  background: #fff;
  align-items: center;
  padding: 2rem;
}
.product-details-img {
  background: black;
  width: 20rem;
  height: 15rem;
  overflow: hidden;
  border-radius: 10px;
}
.product-details-img img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.order-details {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  height: 100%;
  margin-left: 2rem;
}
.order-details p {
  color: #222;
  font-size: 1.2rem;
}
.address-btn {
  background: #b3e140;

  border-radius: 1.5rem;
  display: grid;
  place-items: center;
  cursor: pointer;
  border: none;
  font-weight: bold;
}

@media (max-width: 960px) {
  .address-page {
    flex-direction: column-reverse;
    height: -webkit-max-content;
    height: max-content;
    margin-bottom: 10rem;
  }
  .address-form {
    width: 100%;
  }
  .product-details-address {
    width: 100%;
    margin: 2rem 0;
  }
  .product-details-img {
    width: 15rem;
    height: 10rem;
    border-radius: 20px;
  }
  .order-details h1 {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
  }
}

@media (max-width: 660px) {
  .address-page {
    padding: 1rem 2rem;
  }
  .order-details h1 {
    font-size: 1.2rem;
  }
  .product-details-img {
    width: 10rem;
    height: 7rem;
    border-radius: 10px;
  }
  .product-details-address {
    padding: 1.5rem;
  }
  .address-form {
    padding: 1rem;
  }
  .address-form h1 {
    font-size: 1.5rem;
    text-align: center;
  }
  .address-form form {
    padding: 1rem;
    flex-direction: row;
    flex-wrap: wrap;
  }

  .address-form form label {
    margin-bottom: 0rem;
    margin-left: 0.5rem;
  }
  .address-form form input {
    max-width: 50rem;
    border-radius: 0px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.239);
    margin-bottom: 10px;
  }
  input[type='email'],
  input[type='password'],
  input[type='text'] {
    height: 3rem;
    font-size: 1.2rem;
    padding-left: 0.5rem;
  }
  .order-details p {
    margin: 2px 0;
  }
}

.payment-page {
  width: 100%;
  height: calc(100vh - 6rem);

  padding: 6rem 12rem;
  position: relative;
}
.payment-container {
  width: 100%;
}
.payment-options {
  width: 100%;

  background: #fff;
  padding: 2rem;
  height: -webkit-max-content;
  height: max-content;
  box-shadow: 0 2px 5px rgb(0 0 0 / 24%);
  border-radius: 2rem;
  margin-bottom: 2rem;
}
.payment-option-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.payment {
  padding: 2rem;
  background: white;
  display: flex;
  width: -webkit-max-content;
  width: max-content;
  margin: 3rem;
  align-items: center;
  box-shadow: 0 0.5rem 1rem #00000034;
  border-radius: 4rem;
  border: 1px solid #0000000c;
}
.payment > * {
  margin: 0 1rem;
}
.payment svg {
  color: #b3e140;
  font-size: 4rem;
}
.payment label {
  font-size: 1.4rem;
}

input[type='radio' i] {
  cursor: pointer;
}
.payment-details {
  background: #fff;
  height: -webkit-max-content;
  height: max-content;
  padding: 2rem;
  box-shadow: 0 2px 5px rgb(0 0 0 / 24%);
  border-radius: 2rem;
  width: 100%;
}
.payment-details h2 {
  font-size: 2rem;
}
.payment-details p {
  font-size: 1.3rem;
}
.payment-details span {
  font-size: 1.4rem;
  font-weight: bold;
}
.confirm-btn {
  background: #b3e140;
  border-radius: 1.5rem;
  display: grid;
  place-items: center;
  cursor: pointer;
  border: none;
  font-weight: bold;
}

@media (max-width: 960px) {
  .payment {
    padding: 1rem;

    margin: 1.5rem;
  }
  .payment-page {
    padding: 4rem 4rem;
  }
}
@media (max-width: 560px) {
  .payment-page {
    padding: 2rem 2rem;
  }
  .payment svg {
    font-size: 2rem;
  }
  .payment {
    padding: 1rem;
    background: white;
    display: flex;
    width: 100%;
    margin: 1rem;
    align-items: center;
    box-shadow: 0 0.2rem 0.2rem #00000020;
    border-radius: 4rem;
    border: 1px solid #0000000c;
    justify-content: space-between;
  }
  .payment label {
    font-size: 1.2rem;
  }
  .payment-options h1,
  .payment-details h2 {
    font-size: 1.5rem;
    text-align: center;
    margin-bottom: 1rem;
  }
  .payment-details p {
    margin: 5px 0;
  }
}

.confirmation-msg-container {
  height: calc(100vh - 6rem);
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.confirmation-card {
  padding: 2rem;
  background: #fff;
  margin-top: 2rem;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.239);
  border-radius: 2rem;
  width: 60rem;
}
.confirmation-card video {
  width: 100%;
  pointer-events: none;
}
.confirmation-card h1 {
  text-align: center;
  color: #1cff14;
  font-size: 3rem;
}
.confirmation-card p {
  text-align: center;
  font-size: 1.5rem;
}
.order-confirmation-details {
  padding: 2rem;
  margin-top: 2rem;
  width: 60rem;

  background: #fff;
  height: -webkit-max-content;
  height: max-content;

  box-shadow: 0 2px 5px rgb(0 0 0 / 24%);
  border-radius: 2rem;
}
.order-confirmation-details h2 {
  font-size: 2rem;
}
.order-confirmation-details p {
  font-size: 1.3rem;
}
